export default {
    props:['rating','report','noClick'],
    name:'Rating',
    data(){
        return{
        }
    },
    methods:{
        emitRating(rating){
            if(this.noClick){
                return
            }
            var obj = {
                ...this.report,
                rating
            }
            this.$emit('rating',obj)
        }
    }
}
